"use client";
import React, { useState } from "react";
import Ratings from "../Ratings/ratings";
import MentorMeta from "../../components/"

const mentorprofile = ({username,userImage}) => {


  return (
    <>
      <div className="text-black bg-white rounded-lg shadow-md overflow-hidden flex justify-between items-center px-8 ">
      
      <div className="flex flex-col">
        
            <Ratings />
            <button className="border p-2 px-4 bg-gray-400 text-white rounded-3xl hover:bg-blue-300">
          Send Request
        </button>
          </div>
     

        <div className="flex items-center gap-2 m-3">
          <img
            src={userImage}
            alt="vidhi"
            className="h-[150px] w-[250px] rounded-3xl object-cover"
          />
          
        </div>
       
      </div>
    </>
  );
};

export default mentorprofile;
